import React from "react";
import Board from "./Board.js";
import Argument from "./Argument";
import Back from "./Back";

class ConfirmImaging extends React.Component {
  render() {
    const zone152 = this.props.passed_data.data
      .filter((item) => item.zone === 152)
      .map((i) => (
        <Argument key={i.id} item={i} handleChange={this.props.handleChange} />
      ));

    return (
      <>
        <Back func={() => document.getElementById("ImagingMenu").click()} />
        <h1>
          What is the motivation to get a {this.props.price} {this.props.type}{" "}
          for your patient?
        </h1>
        <Board
          handleDrop={this.props.handleDrop}
          identity={152}
          fill={zone152}
        ></Board>
        <div style={{ display: "flex", justifyContent: "center" }}>
          <button
            id="perform"
            onClick={() => document.getElementById("BrainImaging").click()}
            style={{
              fontSize: "23px",
              fontFamily: "Geneva",
              padding: "10, 10, 10, 10",
              marginTop: "10px"
            }}
            disabled={zone152.length < 1}
          >
            Perform test
          </button>
        </div>
      </>
    );
  }
}

export default ConfirmImaging;
