import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  NavLink
} from "react-router-dom";

//Styles
import "./styles.css";

//data
import pdata from "./data/patientData";
import menuItems from "./data/menuItems.js";
import imageCysts from "./data/mriv2_data";

//Components
import Argument from "./Argument";
import Board from "./Board.js";
import Back from "./Back";
import Question from "./Question";

//Pages
import Feedback from "./Feedback.js";
import Brainexercise from "./pages/Brainexercisev2";
import Instructions from "./Instructions";
import ImagingMenu from "./ImagingMenu";
import ConfirmImaging from "./ConfirmImaging";

//Images
import bg from "./pics/bgofficev2.png";

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      data: pdata,
      current: 0,
      tempText: "",
      qanswers: ["", "", "", "", ""],
      qsubmitted: [false, false, false, false, false],
      diag: "",
      exerciseDone: false,
      menuItems: menuItems,
      cystExerciseDone: false,
      menuDisplay: "",
      currentSup: "",
      sups: imageCysts,
      wrongClicks: 0
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleDrop = this.handleDrop.bind(this);
    this.PatientProfile = this.PatientProfile.bind(this);
    this.Complaint = this.Complaint.bind(this);
    this.QuestionOne = this.QuestionOne.bind(this);
    this.QuestionTwo = this.QuestionTwo.bind(this);
    this.QuestionThree = this.QuestionThree.bind(this);
    this.QuestionFour = this.QuestionFour.bind(this);
    this.QuestionFive = this.QuestionFive.bind(this);
    this.handleQuestionChange = this.handleQuestionChange.bind(this);
    this.Diagnosis = this.Diagnosis.bind(this);
    this.ok = this.ok.bind(this);
    this.Home = this.Home.bind(this);
    this.BrainImaging = this.BrainImaging.bind(this);
    this.handleDisplayArgument = this.handleDisplayArgument.bind(this);
    this.changeFoundAs = this.changeFoundAs.bind(this);
    this.Feedback = this.Feedback.bind(this);
    this.ConfirmImaging = this.ConfirmImaging.bind(this);
  }

  changeFoundAs(type, i) {
    let items = [...this.state.sups];
    let item = { ...items[i] };
    item.foundAs = type;
    items[i] = item;
    this.setState({ sups: items });
  }

  ImagingMenu() {
    return (
      <>
        <ImagingMenu />
      </>
    );
  }

  ConfirmImaging() {
    return (
      <>
        <ConfirmImaging
          passed_data={this.state}
          handleChange={this.handleChange}
          handleDrop={this.handleDrop}
          type="MRI"
          price="$1,100"
        />
      </>
    );
  }

  BrainImaging() {
    return (
      <div>
        <div>
          <Back func={() => document.getElementById("Home").click()} />
        </div>
        <Brainexercise
          passed_data={this.state}
          done={() => this.setState({ cystExerciseDone: true })}
          returnHome={() => document.getElementById("Home").click()}
          handleQuestionChange={this.handleQuestionChange}
          changeFoundAs={(x, i) => this.changeFoundAs(x, i)}
          updateWrongClicks={() =>
            this.setState((prevState) => {
              return { wrongClicks: prevState.wrongClicks + 1 };
            })
          }
        />
      </div>
    );
  }

  Serology() {
    return (
      <div>
        <Back func={() => document.getElementById("Home").click()} />
        <h2>Serology</h2>
      </div>
    );
  }

  handleQuestionChange(event) {
    const { name, value, type, checked } = event.target;
    type === "checkbox"
      ? this.setState({
          [name]: checked
        })
      : this.setState({
          [name]: value
        });
  }

  hqc(event, i) {
    const { value } = event.target;
    let items = [...this.state.qanswers];
    let item = { ...items[i] };
    item = value;
    items[i] = item;
    this.setState({ qanswers: items });
  }

  handleChange(id, text) {
    this.setState((prevState) => (prevState.current = id));
    this.setState({ tempText: text });
  }

  handleDrop(e, identity) {
    e.preventDefault();
    e.stopPropagation();

    if (identity > 100 && identity < 1000) {
      const zone = this.state.data
        .filter((item) => item.zone === identity)
        .map((i) => (
          <Argument
            key={i.id}
            item={i}
            handleChange={this.props.handleChange}
          />
        ));
      for (var a = 0; a < zone.length; a++) {
        if (zone[a].props.item.text === this.state.tempText) {
          return;
        }
      }
      let newItem = {
        id: this.state.data.length + 1000,
        text: this.state.tempText,
        zone: identity
      };
      this.setState({
        data: [...this.state.data, newItem]
      });
    } else {
      this.setState((prevState) => {
        const updatedArguments = prevState.data.map((arg) => {
          if (arg.id === this.state.current) {
            arg.zone = identity;
          }
          return arg;
        });
        return {
          data: updatedArguments
        };
      });
    }
  }

  handleDisplayArgument(identity) {
    this.setState((prevState) => {
      const updatedArguments = prevState.data.map((arg) => {
        if (arg.id === identity) {
          arg.hidden = false;
        }
        return arg;
      });
      return {
        data: updatedArguments
      };
    });
  }

  PatientProfile() {
    const zoneOneEnd = this.state.data
      .filter((item) => item.zone === 1)
      .map((i) => (
        <Argument key={i.id} item={i} handleChange={this.handleChange} />
      ));

    return (
      <div>
        <Back func={() => document.getElementById("Home").click()} />
        <Board
          handleDrop={this.handleDrop}
          identity={1}
          fill={zoneOneEnd}
        ></Board>
      </div>
    );
  }
  Complaint() {
    const zoneTwoEnd = this.state.data
      .filter((item) => item.zone === 2)
      .map((i) => (
        <Argument
          key={i.id}
          item={i}
          handleChange={() => this.handleChange(i.id)}
          passed_data={this.state}
          handleDisplayArgument={() => this.handleDisplayArgument(i.id)}
        />
      ));

    return (
      <div style={{ height: "500px" }}>
        <Back func={() => document.getElementById("Home").click()} />
        <Board
          handleDrop={this.handleDrop}
          identity={2}
          fill={zoneTwoEnd}
        ></Board>
      </div>
    );
  }

  Diagnosis() {
    const zone1000 = this.state.data
      .filter((item) => item.zone === 1000)
      .map((i) => (
        <Argument key={i.id} item={i} handleChange={this.handleChange} />
      ));
    const options = [
      "Active parenchymal NCC",
      "Transitional parenchymal NCC",
      "Inactive parenchymal NCC",
      "Subarachnoid extraparenchymal NCC",
      "Ventricular extraparenchymal NCC",
      "Acute encephalitis",
      "No neurological condition"
    ];
    const selectOptions = options.map((i) => <option value={i}>{i}</option>);

    return (
      <div>
        <Back func={() => document.getElementById("Home").click()} />
        <h3>What is your diagnosis? </h3>
        <Board
          handleDrop={this.handleDrop}
          identity={1000}
          fill={zone1000}
        ></Board>
        <select
          value={this.state.diag}
          name="diag"
          onChange={this.handleQuestionChange}
          style={{ fontSize: "18px", fontFamily: "Geneva" }}
          disabled={
            this.state.exerciseDone
              ? true
              : zone1000.length === 0
              ? true
              : false
          }
        >
          <option value="">-- Please choose a diagnosis --</option>
          {selectOptions}
        </select>
        <button
          onClick={
            this.state.diag === ""
              ? null
              : () => {
                  document.getElementById("Feedback").click();
                  this.setState({ exerciseDone: true });
                }
          }
          disabled={
            this.state.diag === "" ? true : zone1000.length === 0 ? true : false
          }
          style={{ fontSize: "18px", fontFamily: "Geneva" }}
        >
          Final submission
        </button>
      </div>
    );
  }

  ok(i) {
    let newItem = {
      id: this.state.data.length,
      text: this.state.qanswers[i],
      zone: 99
    };
    this.setState({
      data: [...this.state.data, newItem]
    });
    let items = [...this.state.qsubmitted];
    let item = { ...items[i] };
    item = true;
    items[i] = item;
    this.setState({ qsubmitted: items });
  }

  QuestionOne() {
    return (
      <div>
        <Question
          number={101}
          questionText="Classify the epileptic condition"
          selectName="answer1"
          options={[
            "Diffuse cerebral impairment",
            "Focal neurological signs",
            "None of the above"
          ]}
          val={this.state.qanswers[0]}
          qsub={this.state.qsubmitted[0]}
          passed_data={this.state}
          handleChange={this.handleChange}
          handleDrop={this.handleDrop}
          handleQuestionChange={(event) => this.hqc(event, 0)}
          ok={() => this.ok(0)}
        />
      </div>
    );
  }

  QuestionTwo() {
    return (
      <div>
        <Question
          number={102}
          questionText="Is the age of onset indicative of a genetic epileptic condition?"
          selectName="answer2"
          options={[
            "Age of onset suggests genetic cause",
            "Age of onset does not suggest genetic cause"
          ]}
          val={this.state.qanswers[1]}
          qsub={this.state.qsubmitted[1]}
          passed_data={this.state}
          handleChange={this.handleChange}
          handleDrop={this.handleDrop}
          handleQuestionChange={(event) => this.hqc(event, 1)}
          ok={() => this.ok(1)}
        />
      </div>
    );
  }

  QuestionThree() {
    return (
      <div>
        <Question
          number={103}
          questionText="Assess the situation as to progressiveness of the condition."
          selectName="answer3"
          options={["Progressive situation", "Non-progressive situation"]}
          val={this.state.qanswers[2]}
          qsub={this.state.qsubmitted[2]}
          passed_data={this.state}
          handleChange={this.handleChange}
          handleDrop={this.handleDrop}
          handleQuestionChange={(event) => this.hqc(event, 2)}
          ok={() => this.ok(2)}
        />
      </div>
    );
  }

  QuestionFour() {
    return (
      <div>
        <Question
          number={104}
          questionText="WIP"
          selectName="answer4"
          options={["One", "two", "three"]}
          val={this.state.qanswers[3]}
          qsub={this.state.qsubmitted[3]}
          passed_data={this.state}
          handleChange={this.handleChange}
          handleDrop={this.handleDrop}
          handleQuestionChange={(event) => this.hqc(event, 3)}
          ok={() => this.ok(3)}
        />
      </div>
    );
  }

  QuestionFive() {
    return (
      <div>
        <Question
          number={105}
          questionText="WIP"
          selectName="answer5"
          options={["Eins", "Zwei", "Drei"]}
          val={this.state.qanswers[4]}
          qsub={this.state.qsubmitted[4]}
          passed_data={this.state}
          handleChange={this.handleChange}
          handleDrop={this.handleDrop}
          handleQuestionChange={(event) => this.hqc(event, 4)}
          ok={() => this.ok(4)}
        />
      </div>
    );
  }

  Feedback() {
    return <Feedback passed_data={this.state} />;
  }
  Home() {
    const menuZones = this.state.menuItems.map((i) => (
      <area
        shape={i.shape}
        key={i.id}
        id="menuAreas"
        coords={i.coords}
        alt={i.alt}
        onClick={() => document.getElementById(i.alt).click()}
        onMouseEnter={() => this.setState({ menuDisplay: i.text })}
        onMouseLeave={() => this.setState({ menuDisplay: "" })}
      />
    ));
    return (
      <div id="menuContainer">
        <img
          src={bg}
          alt="Office"
          useMap="#workmap"
          width="1000"
          height="500"
        />

        <map name="workmap">{menuZones}</map>
        <div id="menuTitle">
          <h2>{this.state.menuDisplay}</h2>
        </div>
      </div>
    );
  }
  on() {
    document.getElementById("dispInstru").style.display = "block";
  }
  off() {
    document.getElementById("dispInstru").style.display = "none";
    document.getElementById("Home").click();
  }

  render() {
    const zone99End = this.state.data
      .filter((item) => item.zone === 99)
      .map((i) => (
        <Argument key={i.id} item={i} handleChange={this.handleChange} />
      ));

    return (
      <div>
        <div id="dispInstru" onClick={() => this.off()}>
          <Instructions />
        </div>

        <span>
          <Router>
            <NavLink id="Home" to="/Home"></NavLink>

            <NavLink id="PatientProfile" to="/PatientProfile"></NavLink>

            <NavLink id="Complaint" to="/Complaint"></NavLink>

            <NavLink id="BrainImaging" to="/BrainImaging"></NavLink>

            <NavLink id="Serology" to="/Serology"></NavLink>

            <NavLink id="Diagnosis" to="/Diagnosis"></NavLink>

            <NavLink id="QuestionOne" to="/QuestionOne"></NavLink>
            <NavLink id="QuestionTwo" to="/QuestionTwo"></NavLink>
            <NavLink id="QuestionThree" to="/QuestionThree"></NavLink>
            <NavLink id="QuestionFour" to="/QuestionFour"></NavLink>
            <NavLink id="QuestionFive" to="/QuestionFive"></NavLink>

            <NavLink id="Feedback" to="/Feedback"></NavLink>

            <NavLink id="ImagingMenu" to="/ImagingMenu"></NavLink>

            <NavLink id="ConfirmImaging" to="/ConfirmImaging"></NavLink>

            {/* A <Switch> looks through its children <Route>s and
                renders the first one that matches the current URL. */}
            <Switch>
              <Route exact path="/">
                <this.Home />
              </Route>
              <Route path="/Home">
                <this.Home />
              </Route>
              <Route path="/PatientProfile">
                <this.PatientProfile />
              </Route>
              <Route path="/Complaint">
                <this.Complaint />
              </Route>
              <Route path="/BrainImaging">
                <this.BrainImaging />
              </Route>
              <Route path="/Serology">
                <this.Serology />
              </Route>
              <Route path="/Diagnosis">
                <this.Diagnosis />
              </Route>
              <Route path="/ImagingMenu">
                <this.ImagingMenu />
              </Route>
              <Route path="/QuestionOne">
                <this.QuestionOne />
              </Route>
              <Route path="/QuestionTwo">
                <this.QuestionTwo />
              </Route>
              <Route path="/QuestionThree">
                <this.QuestionThree />
              </Route>
              <Route path="/QuestionFour">
                <this.QuestionFour />
              </Route>
              <Route path="/QuestionFive">
                <this.QuestionFive />
              </Route>
              <Route path="/Feedback">
                <this.Feedback />
              </Route>
              <Route path="/ConfirmImaging">
                <this.ConfirmImaging />
              </Route>
            </Switch>
          </Router>

          {!this.state.exerciseDone && (
            <Board
              handleDrop={this.handleDrop}
              identity={99}
              fill={zone99End}
              passed_data={this.state}
            ></Board>
          )}
        </span>

        <div
          style={{
            padding: "10px",
            width: "1000px",
            display: "flex",
            justifyContent: "center"
          }}
        >
          {!this.state.exerciseDone && (
            <button
              id="helpButton"
              onClick={() => this.on()}
              style={{ fontFamily: "Geneva", padding: "10, 10, 10, 10" }}
            >
              Help
            </button>
          )}
        </div>
      </div>
    );
  }
}
export default App;
